/**********fontello*************/

@font-face {
  font-family: 'fontello';
  src: url('../fonts/Fontello/fontello.eot?25590624');
  src: url('../fonts/Fontello/fontello.eot?25590624#iefix') format('embedded-opentype'),
  url('../fonts/Fontello/fontello.woff2?25590624') format('woff2'),
  url('../fonts/Fontello/fontello.woff?25590624') format('woff'),
  url('../fonts/Fontello/fontello.ttf?25590624') format('truetype'),
  url('../fonts/Fontello/fontello.svg?25590624#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
